import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import DHeader from '../Dashboard/DHeader';
import DSubHeader from '../Dashboard/DSubHeader';
import tableDatas from './tableDatas';

import Usermanagement from '../../Images/Usermanagement.png';
import './style/styles.css';

class NewSubscribe extends Component {
  constructor(props){
    super(props);
    this.goBack = this.goBack.bind(this);
  }

  goBack(){
    this.props.history.push('/dashboard');
  }
  render() {
    return (
      <div className="newSubscribe">
        <DHeader />
        <DSubHeader />
        <div className="n-mainTable allcard">          
          <div className="back">
            <button onClick={this.goBack}><i className="fa fa-angle-left" aria-hidden="true"></i> back</button>
          </div>
          <div className="card">
            <div className="card-header">
              <div className="title">
                <img src={Usermanagement} alt="User Management" />
                <p>User Management</p>
              </div>
              <button>Export To Excel</button>
            </div>
            <div className="card-block">
              <div className="row search-func">
                <div className="col-md-4 search-box">
                  <div className="input-group">
                    <input type="text" className="form-control" placeholder="Search by Name / ID" />
                    <span className="input-group-btn">
                      <button className="btn btn-secondary" type="button">Search</button>
                    </span>
                  </div>
                </div>
                <div className="col-md-8 filter-box">
                  <div className="filter-group btn-group">
                    <p className="dropdown-comment">Filter by Group</p>
                    <button type="button" className="btn dropdown-toggle dropdown-toggle-split dropdown-actionbtn" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <span className="sr-only">Toggle Dropdown</span>
                    </button>
                    <div className="dropdown-menu">
                      <button className="dropdown-item" type="button">Security</button>
                      <button className="dropdown-item" type="button">Choir</button>
                      <button className="dropdown-item" type="button">Parkinglot Security</button>
                      <button className="dropdown-item" type="button">Kitchen Stufff</button>
                      <button className="dropdown-item" type="button">Travelling Choir</button>
                    </div>
                  </div>
                  <div className="filter btn-group">
                    <p className="dropdown-comment">Filter by</p>
                    <button type="button" className="btn dropdown-toggle dropdown-toggle-split dropdown-actionbtn" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <span className="sr-only">Toggle Dropdown</span>
                    </button>
                    <div className="dropdown-menu">
                      <button className="dropdown-item" type="button">Member</button>
                      <button className="dropdown-item" type="button">Non-Member</button>
                      <button className="dropdown-item" type="button">Unknown</button>
                      <button className="dropdown-item" type="button">Honored Member</button>
                      <button className="dropdown-item" type="button">Vault</button>
                    </div>
                  </div>
                </div>
              </div>
              <table className="table table-bordered n-main-table">
                <thead>
                  <tr>
                    <th>Subscribe</th>
                    <th>Email</th>
                    <th>Group</th>
                    <th>Member</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    tableDatas.map((tableData, i) =>
                      <TableItem
                        key={i}
                        subscribe={tableData.subscribe}
                        subImage={tableData.subImage}
                        substarImg={tableData.substarImg}
                        email={tableData.email}
                        group={tableData.group}
                        groupPlus={tableData.groupPlus}
                        member={tableData.member} />
                    )
                  }
                </tbody>
              </table>
              <div className="n-table-pagination">
                <nav aria-label="Page navigation example">
                  <ul className="pagination justify-content-center">
                    <li className="page-item">
                      <Link className="page-link" to="#" aria-label="Previous">
                        <i className="fa fa-angle-left" aria-hidden="true"></i>
                      </Link>
                    </li>
                    <li className="page-item"><Link className="page-link" to="#">1</Link></li>
                    <li className="page-item"><Link className="page-link" to="#">2</Link></li>
                    <li className="page-item"><Link className="page-link" to="#">3</Link></li>
                    <li className="page-item"><Link className="page-link" to="#">4</Link></li>
                    <li className="page-item"><Link className="page-link" to="#">5</Link></li>
                    <li className="page-item"><Link className="page-link" to="#">6</Link></li>
                    <li className="page-item">
                      <Link className="page-link" to="#" aria-label="Next">
                        <i className="fa fa-angle-right" aria-hidden="true"></i>
                      </Link>
                    </li>
                  </ul>
                  <div className="table-show">
                    <span>Show:</span>
                    <ul>                      
                      <li>12</li>
                      <li>25</li>
                      <li>50</li>
                      <li>100</li>
                    </ul>
                  </div>
                </nav>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class TableItem extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      showgroupdetail: false
    }
  }

  handleClickgroup = () =>{
    this.setState(prev => ({showgroupdetail: !prev.showgroupdetail}));
  }
  
  render() {
    const { subscribe, subImage, substarImg, email, group, groupPlus, member } = this.props;
    const { showgroupdetail } = this.state;
    return (
      <tr>
        <td>
          <img className="userImg" src={subImage} alt="userImg" />
          <img className="userStar" src={substarImg} alt="userStar" />
          <span className="username">{subscribe}</span>
        </td>
        <td className="table-email">{email}</td>
        <td className="table-group">
          {group}
          {groupPlus === 0 ? null : <span onClick={this.handleClickgroup}>+{groupPlus}</span>}
          {showgroupdetail ? <GroupPlusDetail /> : null}
        </td>
        <td className="table-member">{member}</td>
      </tr>
    );
  }
}

class GroupPlusDetail extends Component{
  render(){
    return (
      <div className="groupPlusDetail">
        <p>Group 02, Group 03, Group 04, Group 05, Group 06</p>
      </div>
    );
  }
}

export default NewSubscribe;


