import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './Pages/Home';
import Login from './Pages/Login';
import ForgotPsw from './Pages/Login/forgotpsw';
import Signup from './Pages/Signup';
import Dashboard from './Pages/Dashboard';
import NewSubscribe from './Pages/NewSubscribe';
import GivingHistory from './Pages/GivingHistory';
import GivingHistoryDetail from './Pages/GivingHistory/GivingHistoryDetail';

class App extends Component {
  render() {
    return (
      <div className="container-fulid">
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/login/forgotpassword" component={ForgotPsw} />
          <Route exact path="/signup" component={Signup} />
          <Route exact path="/dashboard" component={Dashboard} />
          <Route exact path="/new_subscribe" component={NewSubscribe} />
          <Route exact path="/givinghistory" component={GivingHistory} />
          <Route exact path={`/givinghistory/:action`} component={GivingHistoryDetail} />
        </Switch>
      </div>
    );
  }
}

export default App;


