import React, { Component } from 'react';
import LoginIcon from '../../Images/LoginIcon.png';
import TextField from 'material-ui/TextField';
import Close from './Close';
import { Link, Redirect } from 'react-router-dom';
import './style/styles.css';

class Login extends Component {
  constructor() {
    super();
    this.state = {
      redirectDashboard: null,
      redirecthome: null
    };
  }
  
  login = () => {
    this.setState({
      redirectDashboard: `/dashboard`
    })
  }

  close = () => {
    this.setState({
      redirecthome: `/`
    })
  }

  render() {
    let { redirectDashboard, redirecthome } = this.state;

    if( redirectDashboard ){
      return <Redirect to={redirectDashboard} />;
    }

    if ( redirecthome ){
      return <Redirect to={redirecthome} />;
    }
    return (
      <div className="loginform-wrapper">
        <div className="loginform">
          <div className="loginIcon">
            <img src={LoginIcon} alt="LoginIcon" />
            <p>Login</p>
          </div>
          <div className="loginInput">
            <TextField
              id="email"
              label="Email address"
              className="emailAddr"             
              margin="normal"
              fullWidth
            />
            <TextField
              id="password"
              label="Password"
              className="Psw"
              type="password"
              autoComplete="current-password"
              margin="normal"
              fullWidth
            />
          </div>
          <div className="loginbtn">
            <button className="button red submit-btn" onClick={this.login}>Login</button>
            <div className="forgotLink">
              <Link to="/login/forgotpassword">Forgot Password?</Link>
            </div>
          </div>    
          <Close onClick={() => this.close()} />
        </div>        
      </div>
    );
  }
}

export default Login;


